.nearpay-ua-layout {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;

  &__header {
    padding-top: 30px;
    padding-bottom: 41px;
  }

  &__logo {
    height: 40px;
  }
}

.nearpay-ua-main {
  flex-grow: 1;
}

.nearpay-ua-footer {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 30px;
  max-width: 350px;

  &__agreement {
    font-size: 12px;
    line-height: 16px;
    color: #9ca4a9;
    margin-bottom: 20px;
    text-align: center;

    a {
      text-decoration: none;
      color: #6b767d;
    }
  }

  &__support {
    color: #9ca4a9;
    font-weight: 700;
    font-size: 18px;
  }
}
