.nearpay-ua-donate-form .ProceedButton {
  width: 100%;
}

.nearpay-ua-donate-form {
  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 40px;
  }

  &__link {
    margin-top: 40px;
    text-decoration: none;
    font-size: 13px;
    font-weight: 600;
    color: #000000;
    &_gray {
      color: #6b767d;
      font-weight: 400;
    }
    &_icon {
      display: inline;
      vertical-align: middle;
    }
  }
}

.nearpay-ua-donate-form-description {
  font-size: 13px;
  width: 100%;
  margin-bottom: 32px;

  & > div:first-child {
    margin-bottom: 20px;
  }

  &__label {
    font-weight: 600;
    margin-bottom: 8px;
  }
}
