.nearpay-ua-thank-you-container {
  margin-top: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #0b293d;

  a {
    color: #0b293d;
  }

  & > * {
    margin-bottom: 30px;
  }

  &__title {
    font-size: 28px;
    font-weight: 600;
  }

  &__description {
    max-width: 400px;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
  }

  &__help-link {
    background: #e9ebeb;
    border-radius: 10px;
    font-size: 13px;
    font-weight: 600;
    text-transform: uppercase;
    padding: 10px;
    text-decoration: none;
  }

  &__more-link {
    font-size: 13px;
  }
}
